import React, { Component } from "react";
import axios from "axios";

function compare(array1, array2) {
  if (!array1 || !array2) return;
  const temp = [];
  array1 = array1
    .toString()
    .split(",")
    .map(String);
  array2 = array2
    .toString()
    .split(",")
    .map(String);

  for (var i in array1) {
    if (!array2.includes(array1[i])) temp.push(array1[i]);
  }
  for (i in array2) {
    if (!array1.includes(array2[i])) temp.push(array2[i]);
  }
  return temp.sort((a, b) => a - b);
}

export default class QueueList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filePath: props.filePath,
      currentList: [],
      oldList: [],
      value: "",
      oldvalue: "",
      consumers: false,
      errorsCur: null,
      errorsOld: null
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleOldChange = this.handleOldChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.upload = this.upload.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }
  handleOldChange(event) {
    this.setState({ oldvalue: event.target.value });
  }

  handleSubmit(event) {
    this.setState({
      consumers: true
    });

    event.preventDefault();
  }
  async upload(path) {
    const res = await axios.get("/test", {
      headers: {
        path: path
      }
    });
    return res;
  }
  componentDidMount() {
    // let urlPath = window.location.pathname.split("/");
    // let k = urlPath[2] + "/" + urlPath[3];
    // let l = k.split("/");
    // let pathname = l[0] + "\\\\" + l[1] + "\\\\" + "current_queue.txt";
    let pathnameID = this.props.match.params.id;
    let pathnameQueue = this.props.match.params.queue;
    let path = `${pathnameID}` + "/" + `${pathnameQueue}` + "/logs/final.txt";
    this.upload(path)
      .then(response => {
        this.setState({
          currentList: response.data,
          errorsCur: response.data.errors
        });
      })
      .catch(error => {
        this.setState({ errorsCur: error.response.data.message });
        console.log(error.response);
      });

    let pathname2 =
      `${pathnameID}` + "/" + `${pathnameQueue}` + "/logs/past.txt";
    this.upload(pathname2)
      .then(response => {
        this.setState({
          oldList: response.data,
          errorsOld: response.data.errors
        });
      })
      .catch(error => {
        this.setState({ errorsOld: error.response.data.message });
        console.log(error.response);
      });
  }

  render() {
    let comparison;
    if (
      this.state.value === this.state.oldvalue &&
      this.state.value.length > 0
    ) {
      let arrOld;
      let arrCur;
      for (const list of this.state.currentList) {
        if (list.server === this.state.value) {
          arrCur = list.consumers;
        }
      }
      for (const list of this.state.oldList) {
        if (list.server === this.state.oldvalue) {
          arrOld = list.consumers;
        }
      }
      comparison = compare(arrOld, arrCur);
    }
    if (this.state.errorsCur || this.state.errorsOld) {
      return (
        <div className="alert alert-danger">
          <div className="row">{this.state.errorsCur}</div>
          <div className="row">{this.state.errorsOld}</div>
        </div>
      );
    } else {
      return (
        <div className=" container  text-dark">
          <div className="row align-top">
            <div className="col-6 mb-5 mt-5">
              <form
                className="form-control-sm  mb-5 "
                onSubmit={this.handleSubmit}
              >
                <div className="form-group">
                  <div className="form-label">Old Queues</div>
                  <select
                    value={this.state.oldvalue}
                    onChange={this.handleOldChange}
                  >
                    <option>Choose a Queue...</option>
                    {this.state.oldList.map(entry => (
                      <option key={entry.server} value={entry.server}>
                        {entry.server}
                      </option>
                    ))}
                  </select>
                </div>
                <input
                  className="btn btn-primary"
                  type="submit"
                  value="Submit"
                />
              </form>
            </div>
            <div className="col-6 mb-5 mt-5">
              <form
                className="form-control-sm  mb-5"
                onSubmit={this.handleSubmit}
              >
                <div className=" form-group ">
                  <div className="form-label">Current Queues</div>
                  <select value={this.value} onChange={this.handleChange}>
                    <option>Choose a Queue...</option>
                    {this.state.currentList.map(entry => (
                      <option key={entry.server} value={entry.server}>
                        {entry.server}
                      </option>
                    ))}
                  </select>
                </div>
                <input
                  className="btn btn-primary"
                  type="submit"
                  value="Submit"
                />
              </form>
            </div>
          </div>
          <div className="row mb-5 text-dark">
            {comparison != null ? (
              <div className="card mx-auto">
                <div className="sm-shadow card-header font-weight-bold display-5 bg-warning">
                  Consumer Change for <u> {this.state.value}</u>
                </div>
                {comparison.map(compared => (
                  <div className="mx-2">- {compared}</div>
                ))}
              </div>
            ) : (
              ""
            )}
          </div>

          <div className="row  ">
            <div className="col-6 mb-5">
              {this.state.consumers ? (
                <div>
                  {this.state.oldList.map(oldentry => (
                    <div>
                      {oldentry.server === this.state.oldvalue ? (
                        <div className="row border border-dark bg-white">
                          <h3 className="col-10 font-weight-bold text-left">
                            Old Consumers
                          </h3>
                          <div className="col-sm-2 font-weight-light display-5 text-left">
                            {oldentry.consumers.length}
                          </div>

                          {oldentry.consumers.map(consumer => (
                            <div className="small border-top border-dark mb-0">
                              {consumer}
                            </div>
                          ))}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  ))}
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="col-6 mb-5">
              {this.state.consumers ? (
                <div>
                  {this.state.currentList.map(entry => (
                    <div>
                      {entry.server === this.state.value ? (
                        <div className="row border border-dark bg-white">
                          <h3 className="col-10 font-weight-bold text-left">
                            Current Consumers
                          </h3>
                          <div className="col-sm-2 font-weight-light display-5 text-left">
                            {entry.consumers.length}
                          </div>

                          {entry.consumers.map(consumer => (
                            <div className="small border-top border-dark mb-0">
                              {consumer}
                            </div>
                          ))}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  ))}
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
      );
    }
  }
}
